html, body {
  height: 100%;
  width: 100%;
  margin: auto;
}

.content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 32px;
  display: flex;
}

.header {
  text-transform: uppercase;
  font-size: 48px;
}

.grid {
  display: grid;
}

#grid {
  grid-template-rows: repeat(8, 64px);
  grid-template-columns: repeat(5, 64px);
  margin: 24px;
}

.tile {
  text-transform: uppercase;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  margin: 4px;
  font-size: 36px;
  display: flex;
}

.tile[data-type="empty"] {
  background-color: #0000;
  border: 2px solid #d3d6da;
}

.tile[data-type="full"] {
  color: #000;
  background-color: #0000;
  border: 2px solid #878a8c;
}

.tile[data-type="right"] {
  color: #fff;
  background-color: #6aaa64;
  border: none;
}

.tile[data-type="right-letter"] {
  color: #fff;
  background-color: #c9b458;
  border: none;
}

.tile[data-type="wrong"] {
  color: #fff;
  background-color: #787c7e;
  border: none;
}

